 <!--品牌列表-->
<template>
  <div>
    <cms-breadcrumb to="/productType/list" type='list'>
      <li>规格属性列表-{{$route.query.name}}</li>
    </cms-breadcrumb>
    <div class="cms-list-layout">
      <cms-prompt collapse='true'>
        <li> 显示商品类型对应的规格/属性列表信息</li>
        <li>销售属性作为组合商品的SKU信息存在，不作为商品规格参数展示在商品详情内</li>
      </cms-prompt>
      <!-- 筛选区 -->
      <section class="cms-list-query">
        <div class="query-group">
          <label>属性名称:</label>
          <el-input v-model="queryParams.name" @keyup.native.enter='query'></el-input>
        </div>
        <div class="query-group">
          <label>属性值类型:</label>
          <el-select v-model="queryParams.valueType" @change="query">
             <el-option label="全部" value=""></el-option>
            <el-option label="文本" value="1"></el-option>
            <el-option label="单选" value="2"></el-option>
            <el-option label="多选" value="3"></el-option>
            <el-option label="图片(多选)" value="4"></el-option>
          </el-select>
        </div>
        <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
        </div>
      </section>
      <!-- 操作区 -->
      <section class="cms-batch">
        <cms-button icon="add" type="success" size="small"
         v-prem="'/productType/standard/save'"
         @click="$routerLink('/productType/standard/save','save',{
         typeId:$route.query.id,
         typeName:$route.query.name
        })">新增</cms-button>
        <cms-button v-prem="'/productType/standard/delete'" icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
      </section>
      <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
        <el-table-column type="selection" width="46" fixed='left'></el-table-column>
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="name" label="名称" min-width="80"></el-table-column>
        <el-table-column prop="type['name']" label="商品类型" min-width="100">
          <div slot-scope="scope">
            {{scope.row.type.name}}
          </div>
        </el-table-column>
        <el-table-column label="是否为销售属性" min-width="120">
          <div slot-scope="scope">
            <span :class="[scope.row.isSaleAttr?'green':'red']">
              {{scope.row.isSaleAttr?'是':'否'}}
            </span>
          </div>
        </el-table-column>
        <el-table-column label="是否必填" min-width="100">
          <div slot-scope="scope">
            <span :class="[scope.row.required?'green':'red']">
              {{scope.row.required?'是':'否'}}
            </span>
          </div>
        </el-table-column>
        <el-table-column label="属性值类型" min-width="100">
          <div slot-scope="scope">
            <span v-if="scope.row.valueType===1">文本</span>
            <span v-if="scope.row.valueType===2">单选</span>
            <span v-if="scope.row.valueType===3">多选</span>
            <span v-if="scope.row.valueType===4">图片(多选)</span>
          </div>
        </el-table-column>
        <el-table-column prop="attrValues" label="属性值选项" min-width="200">
          <template slot-scope="scope">
             <div class="text-overflow"
             v-if="scope.row.attrValueList.length >0 &&scope.row.attrValueList[0].attrValue!='no'">
               <span v-for="(item,index) in scope.row.attrValueList" :key="index">
                 {{item.attrValue}}{{index==scope.row.attrValueList.length-1?'':','}}
               </span>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width='220' fixed="right" align="center">
          <div slot-scope="scope">
            <cms-button icon='edit' size='small' type='info'
             v-prem="'/productType/standard/update'"
             @click.stop="$routerLink('/productType/standard/update','update',{
              id:scope.row.id,
              typeId:$route.query.id,
              typeName:$route.query.name})">编辑</cms-button>
            <cms-button  v-prem="'/productType/standard/delete'" icon='error' size='small' type='info' @click="deleteData(scope.row.id)">删除</cms-button>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
    </div>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  productStandardPage,
  productStandardDelete
} from '@/api/product/type.js'
var defaultData = {
  name: '',
  page: 1,
  size: 10,
  valueType: '',
  typeId: 0
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(productStandardDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(productStandardDelete, this.ids) // 这个才是真实的删除方法
    }
  },

  created() {
    // 首次触发
    this.queryParams.typeId = this.$route.query.id
    this.getTableData(productStandardPage, this.queryParams)
  }
}
</script>
<style>
</style>



